  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}

.tile:nth-child(odd) {
  color: black;
	background: white;
}

html, body, #root {
  width: 100%;
  height: 100%;
}

#root {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 20px;
}

#board {
  border: 1px solid black;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 100%;
  width: 100%;
}


